.dashboard-panel__toggle-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dashboard-panel__toggle-button {
  border: 0;
}

.dashboard-panel__expandable {
  height: 0;
  overflow: hidden;
}

.broken-link__code {
  color: white;
  padding: 2px 8px;
  border-radius: 10px;
}
.broken-link__code--code-404 {
  background-color: rgb(255, 115, 0);
}
.broken-link__code--code-500 {
  background-color: rgb(221, 1, 1);
}